import React from 'react'

import { ToastContainer,toast } from 'react-toastify';

import { useForm,SubmitHandler } from 'react-hook-form';
import { useSelector } from 'react-redux';

import { IShippingFields } from '../../types/Form';

import 'react-toastify/dist/ReactToastify.css';
import './Form.scss';
import { selectCart } from '../../redux/slices/cartSlice';

const Form:React.FC = () => {
	const {items} = useSelector(selectCart)
	const notify = () => toast.success(' ', {
		position: 'top-right',
		autoClose: 5000,
		hideProgressBar: false,
		closeOnClick: true,
		pauseOnHover: true,
		draggable: true,
		progress: undefined,
		theme: 'dark',
	});

	
  
	const {
		register,
		// control,
		handleSubmit,
		formState: { errors },
		reset,
	} = useForm<IShippingFields>({
		mode: 'onChange',
	})
	const onSubmit:SubmitHandler<IShippingFields> = (data) => {
		const order = {
			...data,
			...items,
		}
		notify()
	
		console.log(' :',order);
	 reset()
	}
	// /^\+?(\d{1,3})?[- .]?\(?(?:\d{2,3})\)?[- .]?\d\d\d[- .]?\d\d\d\d$/
	return (
		<div className="Form">
			<h2 className="Form-title">
				   -       
			</h2>
			<div className="Form-wrapper">
				<form onSubmit={handleSubmit(onSubmit)} className="Form-form">
					<div className="Form-inputs">
						<label className="Form-label">
							<span></span>
							<input
								
								{...register('name', {
									required: 'Name is require field!',
									maxLength: {
										value: 15,
										message: '   15 ',
									},
									minLength: {
										value: 3,
										message: '   3 ',
									},
								})}
								className="input-reset"
								type="text"
								placeholder=" "
							/>
							{errors.name && (
								<div style={{ color: 'red' }}>
									{errors?.name?.message}
								</div>
							)}
						</label>
						<label className="Form-label">
							<span> </span>
							<input
								{...register('phone', {
									required: 'Phone is require field!',

									maxLength: {
										value: 25,
										message: '   15 ',
									},
									minLength: {
										value: 9,
										message: '   3 ',
									},
									pattern: {
										value: /^\+?\d{1,4}?[-.\s]?\(?\d{1,3}?\)?[-.\s]?\d{1,4}[-.\s]?\d{1,4}[-.\s]?\d{1,9}$/,
										message: 'Please enter valid phone',
									},
								})}
								className="input-reset"
								type="text"
								placeholder="+7 (___) ___-__-__"
							/>
							{errors.phone && (
								<div style={{ color: 'red' }}>
									{errors?.phone?.message}
								</div>
							)}
						</label>
						<label className="Form-label">
							<span>E-mail</span>
							<input
								{...register('email', {
									required: 'Email is require field!',
									maxLength: 30,
									minLength: 10,
									pattern: {
										// eslint-disable-next-line max-len
										value: /^[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?$/,

										message: 'Please enter valid email!',
									},
								})}
								className="input-reset"
								type="text"
								placeholder=" "
							/>
							{errors.email && (
								<div style={{ color: 'red' }}>
									{errors?.email?.message}
								</div>
							)}
						</label>
					</div>
					<div className="Form-info">
						<p>
							   , ,  
							  {' '}
							<a href="/">
								    
								
							</a>
							,      
						</p>
						<button disabled={items.length === 0}  className="btn-reset Form-btn" type="submit">
							 
						</button>
					</div>
				</form>
			</div>
			<button onClick={notify}>csad</button>
			<ToastContainer
				position="top-right"
				autoClose={5000}
				hideProgressBar={false}
				newestOnTop={false}
				closeOnClick
				rtl={false}
				pauseOnFocusLoss
				draggable
				pauseOnHover
				theme="dark"
			/>
		</div>
	)
}

export default Form
