import React,{useRef} from 'react'
import { SubmitHandler, useForm } from 'react-hook-form'

import { ToastContainer,toast } from 'react-toastify';
import emailjs from '@emailjs/browser';

import { IServiceFormFields } from '../../types/Form'

import styles from './ServiceForm.module.scss'


const ServiceForm: React.FC = () => {
	const notify = () => toast.success('!', {
		position: 'top-right',
		autoClose: 5000,
		hideProgressBar: false,
		closeOnClick: true,
		pauseOnHover: true,
		draggable: true,
		progress: undefined,
		theme: 'dark',
	});
	const {
		register,
		handleSubmit,
		formState: { errors },
		reset,
	} = useForm<IServiceFormFields>({
		mode: 'onChange',
	})
	const sendEmail = () => {
	
		emailjs.sendForm('service_c5fenef', 'template_xoiy8ec', form.current, 'DQkHD520RftRbJ7wx')
		  .then((result) => {
			  console.log(result.text);
		  }, (error) => {
			  console.log(error.text);
		  });

	  };

	const onSubmit: SubmitHandler<IServiceFormFields> = data => {
		console.log(data)
		sendEmail();
		notify()
		reset()
	}
	const form = useRef<any>();

 
	return (
		<div className={styles.ServiceForm__wrapper}>
			<form ref={form} onSubmit={handleSubmit(onSubmit)} className={styles.ServiceForm}>
				<div className={styles.ServiceForm__top}>
					<label className={styles.ServiceForm__input}>
						<span></span>
						<input
							{...register('theme', {
								maxLength: {
									value: 30,
									message: '   30 ',
								},
								minLength: {
									value: 8,
									message: '   8 ',
								},
							})}
							type="text"
							className={`input-reset ${styles.focus}`}
							placeholder="  "
						/>
						<span className={styles.focusBorder}></span>
						{errors.theme && (
							<div style={{ color: 'red' }}>
								{errors?.theme?.message}
							</div>
						)}
					</label>
					<label className={styles.ServiceForm__input}>
						<span>Email</span>
						<input
							{...register('email', {
								required: 'Email is require field!',
								maxLength: 30,
								minLength: 10,
								pattern: {
									// eslint-disable-next-line max-len
									value: /^[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?$/,

									message: 'Please enter valid email!',
								},
							})}
							type="email"
							className={`input-reset ${styles.focus}`}
							placeholder=" "
						/>
						<span className={styles.focusBorder}></span>
						{errors.email && (
							<div style={{ color: 'red' }}>
								{errors?.email?.message}
							</div>
						)}
					</label>
					<label className={styles.ServiceForm__input}>
						<span></span>
						<input
							{...register('name', {
								required: 'Name is require field!',
								maxLength: {
									value: 15,
									message: '   15 ',
								},
								minLength: {
									value: 3,
									message: '   3 ',
								},
							})}
							type="text"
							className={`input-reset ${styles.focus}`}
							placeholder=""
						/>
						<span className={styles.focusBorder}></span>
						{errors.name && (
							<div style={{ color: 'red' }}>
								{errors?.name?.message}
							</div>
						)}
					</label>
					<label className={styles.ServiceForm__input}>
						<span></span>
						<input
							{...register('phone', {
								required: 'Phone is require field!',

								maxLength: {
									value: 25,
									message: '   15 ',
								},
								minLength: {
									value: 9,
									message: '   3 ',
								},
								pattern: {
									value: /^\+?\d{1,4}?[-.\s]?\(?\d{1,3}?\)?[-.\s]?\d{1,4}[-.\s]?\d{1,4}[-.\s]?\d{1,9}$/,
									message: 'Please enter valid phone',
								},
							})}
							type="phone"
							className={`input-reset ${styles.focus}`}
							placeholder="+7 (___) ___-__-__"
						/>
						<span className={styles.focusBorder}></span>
						{errors.phone && (
							<div style={{ color: 'red' }}>
								{errors?.phone?.message}
							</div>
						)}
					</label>
				</div>
				<div className={styles.ServiceForm__bottom}>
					<span></span>
					<div className={styles.ServiceForm__textarea}>
						<textarea
							{...register('text', {
								required: 'Text is require field!',

								maxLength: {
									value: 350,
									message: '   350 ',
								},
								minLength: {
									value: 10,
									message: '   10 ',
								},
							})}
							className={`input-reset ${styles.focuss}`}
							placeholder="  "></textarea>
						<span className={styles.focusBorder}>
							<i></i>
						</span>
						{errors.text && (
							<div style={{ color: 'red' }}>
								{errors?.text?.message}
							</div>
						)}
					</div>
				</div>
				<button
					type="submit"
					className={`btn-reset ${styles.ServiceForm__btn}`}>
					
				</button>
			</form>
			<ToastContainer
				position="top-right"
				autoClose={5000}
				hideProgressBar={false}
				newestOnTop={false}
				closeOnClick
				rtl={false}
				pauseOnFocusLoss
				draggable
				pauseOnHover
				theme="dark"
			/>
		</div>
	)
}

export default ServiceForm
