import { FC } from 'react'

import { Swiper, SwiperSlide } from 'swiper/react'
import { Navigation, Pagination, A11y } from 'swiper/modules'

import 'swiper/css'
import SliderNavigation from '../SliderNavigation/SliderNavigation'

import Button from '../Button/Button'

import styles from './Quantity.module.scss'

const Quantity: FC = () => {
	return (
		<section className={`${styles.quantity}`}>
			<Swiper
				// @ts-ignore
				modules={[Pagination, Navigation, A11y]}
				pagination={{ clickable: true }}
				className="quantity__slider">
				<SwiperSlide>
					<div className={`${styles.quantity__slide}`}>
						<div className={`${styles.quantity__slide_text}`}>
							<h1 className={`${styles.quantity__title}`}>
								   
							</h1>
							<div className={`${styles.quantity__descr}`}>
								 Tuotown      
								  
							</div>
						</div>
						{/* <button className={`btn-reset ${styles.quantity__btn}`}>
							
						</button> */}
						<Button 
							size="medium" 
							variant="secondary" 
							type="two"
							onclick={()=> console.log('click')}
							otherClass={styles.quantity__btn}></Button>
					</div>
				</SwiperSlide>
				<SwiperSlide>
					<div className={`${styles.quantity__slide}`}>
						<div className={`${styles.quantity__slide_text}`}>
							<h1 className={`${styles.quantity__title}`}>
								   
							</h1>
							<div className={`${styles.quantity__descr}`}>
								 Tuotown      
								  
							</div>
						</div>
						<Button 
							size="medium" 
							variant="secondary" 
							type="two"
							onclick={()=> console.log('click')}
							otherClass={styles.quantity__btn}></Button>
					</div>
				</SwiperSlide>
				<SwiperSlide>
					<div className={`${styles.quantity__slide}`}>
						<div className={`${styles.quantity__slide_text}`}>
							<h1 className={`${styles.quantity__title}`}>
								   
							</h1>
							<div className={`${styles.quantity__descr}`}>
								 Tuotown      
								  
							</div>
						</div>
						<Button 
							size="medium" 
							variant="secondary" 
							type="two"
							onclick={()=> console.log('click')}
							otherClass={styles.quantity__btn}></Button>
					</div>
				</SwiperSlide>
				<SliderNavigation />
			</Swiper>
		</section>
	)
}

export default Quantity
