import styles from './Home.module.scss';

const Home = () => {
	return (
		<section className={styles.Home}>
			<div className={`container ${styles.Home__container}`}>
      	<h1 className={styles.Home__title}>  </h1>
				<span className={styles.Home__subtitle}>         </span>
			</div>
		</section>
	)
}

export default Home